"use client";

import React from 'react';
import { motion } from 'framer-motion';
import { MessageSquareText, SlidersHorizontal, LineChart } from 'lucide-react';

export type Mode = 'chat' | 'configure' | 'analytics';

interface ModeSelectorProps {
  currentMode: Mode;
  onModeChange: (mode: Mode) => void;
}

export default function ModeSelector({ currentMode, onModeChange }: ModeSelectorProps) {
  const modes: { id: Mode; label: string; icon: React.ReactNode }[] = [
    { id: 'chat', label: 'Live Operator', icon: <MessageSquareText size={13} strokeWidth={2} /> },
    { id: 'configure', label: 'Strategy Config', icon: <SlidersHorizontal size={13} strokeWidth={2} /> },
    { id: 'analytics', label: 'Pipeline Intel', icon: <LineChart size={13} strokeWidth={2} /> },
  ];

  return (
    <div className="flex items-center rounded-full bg-zinc-900/70 p-0.5 ring-1 ring-white/10 backdrop-blur-sm">
      {modes.map((mode) => (
        <button
          key={mode.id}
          onClick={() => onModeChange(mode.id)}
          className={`relative flex items-center gap-1.5 px-3 py-1.5 text-xs font-medium transition-colors outline-none focus-visible:ring-2 focus-visible:ring-indigo-500/50 rounded-full ${
            currentMode === mode.id ? 'text-white' : 'text-zinc-500 hover:text-zinc-300'
          }`}
        >
          {currentMode === mode.id && (
            <motion.div
              layoutId="active-mode-pill"
              className="absolute inset-0 z-0 rounded-full bg-gradient-to-b from-zinc-700 to-zinc-800 shadow-[inset_0_1px_0_0_rgba(255,255,255,0.1)] ring-1 ring-zinc-950/40"
              transition={{ type: 'spring', stiffness: 350, damping: 25 }}
            />
          )}
          <span className="relative z-10 flex items-center gap-1.5 mix-blend-plus-lighter">
            {mode.icon}
            {mode.label}
          </span>
        </button>
      ))}
    </div>
  );
}