"use client";

import React, { useState, useRef, useEffect } from 'react';
import { Bot, MoreHorizontal, BellDot, Zap, ShieldAlert } from 'lucide-react';
import ModeSelector, { Mode } from './ModeSelector';
import ChatMessage, { Message } from './ChatMessage';
import ChatInput from './ChatInput';
import IntelligencePanel from './IntelligencePanel';

const INITIAL_MESSAGES: Message[] = [
  {
    id: '1',
    role: 'agent',
    type: 'briefing',
    content: "Morning briefing: Q4 pipeline velocity has decreased by 12%. Three key Enterprise opportunities are stalled at negotiations. Immediate action required on Acme Corp and Globex.",
    timestamp: '09:00 AM',
    confidence: 98,
    intent: 'Strategic Alert'
  },
  {
    id: '2',
    role: 'user',
    content: "Focus on Acme Corp. What's the latest blocker?",
    timestamp: '09:02 AM',
  },
  {
    id: '3',
    role: 'agent',
    type: 'alert',
    content: "Risk detected: Acme's champion, Sarah Chen, has gone silent for 5 days. Competitor 'SalesForceZero' was mentioned in their recent earnings call.",
    timestamp: '09:02 AM',
    confidence: 95,
    intent: 'Risk Assessment'
  },
  {
    id: '4',
    role: 'agent',
    type: 'standard',
    content: "I recommend a multi-threaded approach. I've prepared a draft to re-engage Sarah and another for their CFO highlighting our ROI calculator based on their recent usage data.",
    timestamp: '09:03 AM',
    confidence: 92,
    intent: 'Tactical Recommendation',
    actions: [
      { label: 'Review Champion Draft', type: 'primary' },
      { label: 'Review CFO Draft', type: 'secondary' },
      { label: 'Schedule Strategy Sync', type: 'secondary' }
    ]
  }
];

export default function SalesAgentUI() {
  const [activeMode, setActiveMode] = useState<Mode>('chat');
  const [showIntelligence, setShowIntelligence] = useState(true);
  const [messages, setMessages] = useState<Message[]>(INITIAL_MESSAGES);
  const scrollRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop = scrollRef.current.scrollHeight;
    }
  }, [messages]);

  const handleSendMessage = (text: string) => {
    const newMessage: Message = {
      id: Date.now().toString(),
      role: 'user',
      content: text,
      timestamp: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
    };
    setMessages(prev => [...prev, newMessage]);
  };

  return (
    <div className="flex h-full w-full flex-col overflow-hidden bg-zinc-950 bg-[radial-gradient(ellipse_at_top,_var(--tw-gradient-stops))] from-zinc-900 via-zinc-950 to-zinc-950">
      {/* Command Center Header */}
      <header className="flex h-14 shrink-0 items-center justify-between border-b border-white/5 bg-zinc-950/50 px-4 backdrop-blur-md z-20">
        <div className="flex items-center gap-3">
          <div className="flex h-8 w-8 items-center justify-center rounded-lg bg-gradient-to-br from-indigo-500/20 to-purple-500/20 text-indigo-300 ring-1 ring-inset ring-indigo-500/30">
            <Bot size={18} strokeWidth={1.5} />
          </div>
          <div>
            <h1 className="text-sm font-semibold leading-none text-zinc-100 tracking-tight">Sales Assistant AI</h1>
            <div className="mt-1 flex items-center gap-1.5">
              <span className="relative flex h-1.5 w-1.5">
                <span className="absolute inline-flex h-full w-full animate-ping rounded-full bg-emerald-400 opacity-75"></span>
                <span className="relative inline-flex h-1.5 w-1.5 rounded-full bg-emerald-500"></span>
              </span>
              <span className="text-[10px] font-medium text-zinc-400 uppercase tracking-wider">Live • Closing Mode Active</span>
            </div>
          </div>
        </div>

        <ModeSelector currentMode={activeMode} onModeChange={setActiveMode} />

        <div className="flex items-center gap-3">
          <div className="flex items-center gap-1 rounded-full border border-rose-500/20 bg-rose-500/10 px-2 py-0.5 text-[10px] font-medium text-rose-300">
            <ShieldAlert size={10} />
            <span>2 Risks Detected</span>
          </div>
          <div className="h-4 w-px bg-zinc-800" />
          <button className="relative text-zinc-400 hover:text-zinc-200 transition-colors">
            <BellDot size={18} strokeWidth={1.5} />
            <span className="absolute right-0 top-0 h-2 w-2 rounded-full bg-rose-500 ring-2 ring-zinc-950" />
          </button>
          <button className="text-zinc-400 hover:text-zinc-200 transition-colors">
            <MoreHorizontal size={18} strokeWidth={1.5} />
          </button>
        </div>
      </header>

      {/* Main Workspace Layout */}
      <div className="flex flex-1 overflow-hidden relative">
        {/* Chat Core */}
        <div className="flex-1 flex flex-col min-w-0">
          <div 
            ref={scrollRef}
            className="flex-1 overflow-y-auto px-4 py-6 scrollbar-thin scrollbar-track-transparent scrollbar-thumb-zinc-800"
          >
            <div className="mx-auto max-w-3xl flex flex-col gap-6 pb-8">
               <div className="flex items-center justify-center pb-2 opacity-50">
                  <span className="text-[10px] font-medium uppercase tracking-widest text-zinc-500">Dec 14 • Executive Briefing</span>
               </div>
              {messages.map((msg) => (
                <ChatMessage key={msg.id} message={msg} />
              ))}
            </div>
          </div>

          {/* Input Dock */}
          <div className="shrink-0 z-20 bg-gradient-to-t from-zinc-950 via-zinc-950/90 to-transparent pb-6 px-4 pt-4">
            <div className="mx-auto max-w-3xl relative">
              <ChatInput onSend={handleSendMessage} />
               <div className="absolute -bottom-4 inset-x-0 flex items-center justify-center gap-1.5 text-[9px] font-medium text-zinc-600 uppercase tracking-wider">
                 <Zap size={10} className="text-indigo-500" fill="currentColor" />
                 <span>AI acts as a strategic advisor. Verification recommended for financial commits.</span>
               </div>
            </div>
          </div>
        </div>

        {/* Intelligence Right Rail */}
        <IntelligencePanel isOpen={showIntelligence} onToggle={() => setShowIntelligence(!showIntelligence)} />
      </div>
    </div>
  );
}