"use client";

import React from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { PanelRightClose, PanelRightOpen, Building2, TrendingUp, AlertTriangle, ArrowUpRight, Activity, Target } from 'lucide-react';

interface IntelligencePanelProps {
  isOpen: boolean;
  onToggle: () => void;
}

export default function IntelligencePanel({ isOpen, onToggle }: IntelligencePanelProps) {
  return (
    <>
      {/* Toggle Button (When Closed) */}
      <AnimatePresence>
        {!isOpen && (
          <motion.button
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: 20 }}
            onClick={onToggle}
            className="absolute right-4 top-4 z-30 flex h-8 w-8 items-center justify-center rounded-lg border border-zinc-800 bg-zinc-950/50 text-zinc-400 backdrop-blur-md hover:border-zinc-700 hover:text-zinc-200 transition-colors"
          >
            <PanelRightOpen size={16} />
          </motion.button>
        )}
      </AnimatePresence>

      {/* The Panel */}
      <motion.div
        initial={false}
        animate={{ width: isOpen ? 320 : 0, opacity: isOpen ? 1 : 0 }}
        transition={{ type: "spring", stiffness: 300, damping: 30 }}
        className="relative h-full shrink-0 overflow-hidden border-l border-white/5 bg-zinc-950/30 backdrop-blur-xl z-10"
      >
        <div className="h-full w-[320px] overflow-y-auto scrollbar-thin scrollbar-thumb-zinc-800">
          {/* Header */}
          <div className="sticky top-0 z-10 flex h-14 items-center justify-between border-b border-white/5 bg-zinc-950/80 px-4 backdrop-blur-md">
            <div className="flex items-center gap-2">
                <Activity size={14} className="text-indigo-400" />
                <h2 className="text-xs font-semibold uppercase tracking-wider text-zinc-100">Context Intelligence</h2>
            </div>
            <button onClick={onToggle} className="text-zinc-500 hover:text-zinc-300 transition-colors">
              <PanelRightClose size={16} />
            </button>
          </div>

          {/* Content */}
          <div className="flex flex-col gap-6 p-4">
            
            {/* Key Entity */}
            <div className="rounded-xl border border-zinc-800/50 bg-zinc-900/30 p-4">
              <div className="flex items-start justify-between">
                <div className="flex items-center gap-3">
                  <div className="flex h-10 w-10 items-center justify-center rounded-lg bg-zinc-800">
                    <Building2 size={18} className="text-zinc-400" />
                  </div>
                  <div>
                    <h3 className="text-sm font-semibold text-zinc-100">Acme Corp</h3>
                    <p className="text-[11px] font-medium text-zinc-500 uppercase tracking-wider">Enterprise Prospect</p>
                  </div>
                </div>
                <span className="flex items-center gap-1 rounded-full bg-emerald-500/10 px-2 py-0.5 text-[10px] font-medium text-emerald-400 border border-emerald-500/20">
                  Active Deal
                </span>
              </div>
            </div>

            {/* Deal Score */}
            <div>
                <div className="flex items-center justify-between mb-2">
                    <h4 className="text-[11px] font-medium uppercase tracking-wider text-zinc-400 flex items-center gap-1.5">
                        <Target size={12} /> Deal Probability
                    </h4>
                    <span className="text-sm font-bold text-emerald-400">72%</span>
                </div>
                <div className="h-2 w-full rounded-full bg-zinc-800/50 overflow-hidden">
                    <motion.div 
                        initial={{ width: 0 }}
                        animate={{ width: '72%' }}
                        transition={{ duration: 1, delay: 0.2 }}
                        className="h-full rounded-full bg-gradient-to-r from-indigo-500 to-emerald-500" 
                    />
                </div>
                <p className="mt-2 text-[10px] text-zinc-500 flex items-center gap-1">
                    <TrendingUp size={10} className="text-emerald-500" /> Up 4% since last week due to CFO engagement.
                </p>
            </div>

            <div className="h-px bg-zinc-800/50" />

            {/* Signals */}
            <div className="space-y-3">
                <h4 className="text-[11px] font-medium uppercase tracking-wider text-zinc-400 flex items-center gap-1.5">
                    <Activity size={12} /> Buying Signals (Last 7d)
                </h4>
                <ul className="space-y-2">
                    <li className="flex items-start gap-2 text-xs text-zinc-300">
                        <span className="mt-0.5 block h-1.5 w-1.5 rounded-full bg-indigo-500 shadow-[0_0_8px_rgba(99,102,241,0.6)]" />
                        CFO viewed 'Enterprise Security' doc (3x).
                    </li>
                    <li className="flex items-start gap-2 text-xs text-zinc-300">
                        <span className="mt-0.5 block h-1.5 w-1.5 rounded-full bg-indigo-500 shadow-[0_0_8px_rgba(99,102,241,0.6)]" />
                        Added 2 new seats to trial account.
                    </li>
                </ul>
            </div>

            <div className="h-px bg-zinc-800/50" />

            {/* Risks */}
             <div className="space-y-3">
                <h4 className="text-[11px] font-medium uppercase tracking-wider text-rose-400 flex items-center gap-1.5">
                    <AlertTriangle size={12} /> Active Risks
                </h4>
                <div className="rounded-lg border border-rose-900/30 bg-rose-950/10 p-3">
                    <p className="text-xs font-medium text-rose-200">Champion Silence</p>
                    <p className="mt-1 text-[11px] text-rose-300/70 leading-relaxed">Sarah Chen hasn't replied to the technical validation email sent 5 days ago.</p>
                </div>
            </div>

             {/* Next Best Action */}
             <div className="mt-auto pt-4">
                 <button className="group relative w-full overflow-hidden rounded-xl bg-gradient-to-br from-indigo-600 to-purple-600 p-4 text-left transition-transform active:scale-[0.98]">
                     <div className="absolute inset-0 bg-white/0 transition-colors group-hover:bg-white/10" />
                     <h4 className="text-[10px] font-semibold uppercase tracking-wider text-indigo-100/80 mb-1 flex items-center gap-1">
                         Suggested Next Step <ArrowUpRight size={12} />
                     </h4>
                     <p className="text-sm font-semibold text-white">Schedule Executive Alignment with CFO</p>
                     <p className="text-[11px] text-indigo-100/70 mt-1">AI can draft the invite based on their recent security concerns.</p>
                 </button>
             </div>

          </div>
        </div>
      </motion.div>
    </>
  );
}